.showcase {
  display: flex;
  flex-direction: column;
  padding: 2em;
}

.showcase .boxed-expression {
  max-height: 55em;
  overflow-y: auto;
}

.showcase .updated-json {
  margin-top: 5em;
  border-style: inset;
  padding: 1em;
  max-height: 30em;
  overflow-y: auto;
}

.showcase .updated-json .disclaimer {
  font-style: italic;
  color: #4d5258;
}
