.expression-container .literal-expression .pf-c-drawer__splitter {
  background-color: inherit;
}

.expression-container .literal-expression .pf-c-drawer {
  --pf-c-drawer__splitter--after--BorderLeftWidth: var(
    --pf-global--BorderWidth--sm
  );
  position: absolute;
  min-width: 0.625rem;
  right: 0;
  top: 0;
  bottom: 0;
}

.literal-expression .literal-expression-header .expression-info {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.literal-expression .literal-expression-header {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  position: relative;
  padding: 5px;
  border-style: ridge;
  background-color: #DEF3FF;
  color: black;
}

.literal-expression .literal-expression-header .expression-data-type {
   font-style: normal;
}

.literal-expression .literal-expression-body {
  height: 4em;
}

.literal-expression .literal-expression-body textarea {
  padding-bottom: 0;
  width: 100%;
  height: 100%;
  resize: none;
}
